import { decorateIcons } from './common-decorators.js';

// Save a list of named combobox actions, for future readability
const SelectActions = {
  Close: 0,
  CloseSelect: 1,
  First: 2,
  Last: 3,
  Next: 4,
  Open: 5,
  PageDown: 6,
  PageUp: 7,
  Previous: 8,
  Select: 9,
  Type: 10,
  Tab: 11,
  ShiftTab: 12,
};

/*
 * Helper functions
 */

// filter an array of options against an input string
// returns an array of options that begin with the filter string, case-independent
function filterOptions(filter, options = [], exclude = []) {
  return options.filter((option) => {
    const matches = option.toLowerCase().indexOf(filter.toLowerCase()) === 0;
    return matches && exclude.indexOf(option) < 0;
  });
}

// map a key press to an action
function getActionFromKey(event, menuOpen) {
  const { key, altKey, ctrlKey, metaKey } = event;
  const openKeys = ['ArrowDown', 'ArrowUp', 'Enter', ' ']; // all keys that will do the default open action
  // handle opening when closed
  if (!menuOpen && openKeys.includes(key)) {
    return SelectActions.Open;
  }

  // home and end move the selected option when open or closed
  if (key === 'Home') {
    return SelectActions.First;
  }
  if (key === 'End') {
    return SelectActions.Last;
  }

  // handle typing characters when open or closed
  if (key === 'Backspace' || key === 'Clear' || (key.length === 1 && key !== ' ' && !altKey && !ctrlKey && !metaKey)) {
    return SelectActions.Type;
  }

  // handle keys when open
  if (menuOpen) {
    if (key === 'ArrowUp' && altKey) {
      return SelectActions.CloseSelect;
    }
    if (key === 'ArrowDown' && !altKey) {
      return SelectActions.Next;
    }
    if (key === 'ArrowUp') {
      return SelectActions.Previous;
    }
    if (key === 'PageUp') {
      return SelectActions.PageUp;
    }
    if (key === 'PageDown') {
      return SelectActions.PageDown;
    }
    if (key === 'Escape') {
      return SelectActions.Close;
    }
    if (key === 'Enter' || key === ' ') {
      return SelectActions.CloseSelect;
    }
    if (event.shiftKey && event.keyCode === 9) {
      return SelectActions.ShiftTab;
    }
    if (key === 'Tab') {
      return SelectActions.Tab;
    }
  }
  return null;
}

// return the index of an option from an array of options, based on a search string
// if the filter is multiple iterations of the same letter (e.g "aaa"), then cycle through first-letter matches
function getIndexByLetter(options, filter, startIndex = 0) {
  const orderedOptions = [...options.slice(startIndex), ...options.slice(0, startIndex)];
  const firstMatch = filterOptions(filter, orderedOptions)[0];
  const allSameLetter = (array) => array.every((letter) => letter === array[0]);

  // first check if there is an exact match for the typed string
  if (firstMatch) {
    return options.indexOf(firstMatch);
  }

  // if the same letter is being repeated, cycle through first-letter matches
  if (allSameLetter(filter.split(''))) {
    const matches = filterOptions(filter[0], orderedOptions);
    return options.indexOf(matches[0]);
  }

  // if no matches, return -1
  return -1;
}

// get an updated option index after performing an action
function getUpdatedIndex(currentIndex, maxIndex, action) {
  const pageSize = 10; // used for pageup/pagedown

  switch (action) {
    case SelectActions.First:
      return 0;
    case SelectActions.Last:
      return maxIndex;
    case SelectActions.Previous:
    case SelectActions.ShiftTab:
      return Math.max(0, currentIndex - 1);
    case SelectActions.Next:
    case SelectActions.Tab:
      return Math.min(maxIndex, currentIndex + 1);
    case SelectActions.PageUp:
      return Math.max(0, currentIndex - pageSize);
    case SelectActions.PageDown:
      return Math.min(maxIndex, currentIndex + pageSize);
    default:
      return currentIndex;
  }
}

// check if element is visible in browser view port
function isElementInView(element) {
  const bounding = element.getBoundingClientRect();

  return (
    bounding.top >= 0 &&
    bounding.left >= 0 &&
    bounding.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
    bounding.right <= (window.innerWidth || document.documentElement.clientWidth)
  );
}

// check if an element is currently scrollable
function isScrollable(element) {
  return element && element.clientHeight < element.scrollHeight;
}

// ensure a given child element is within the parent's visible scroll area
// if the child is not visible, scroll the parent
function maintainScrollVisibility(activeElement, scrollParent) {
  const { offsetHeight, offsetTop } = activeElement;
  const { offsetHeight: parentOffsetHeight, scrollTop } = scrollParent;

  const isAbove = offsetTop < scrollTop;
  const isBelow = offsetTop + offsetHeight > scrollTop + parentOffsetHeight;

  if (isAbove) {
    scrollParent.scrollTo(0, offsetTop);
  } else if (isBelow) {
    scrollParent.scrollTo(0, offsetTop - parentOffsetHeight + offsetHeight);
  }
}

function createOwnElement({ icon = null, name = '', reversed = false }) {
  const el = document.createElement('div');
  el.classList.add('combo', 'js-select');
  const iconToRender = icon ? `<span class="icon icon-lib-${icon} select-icon ${reversed ? 'reversed' : ''}"></span>` : '';
  el.innerHTML = `
      <div 
        aria-controls="listbox1" 
        aria-expanded="false" 
        aria-haspopup="listbox" 
        aria-labelledby="combo-label-${name}" 
        id="combo-${name}"
        class="combo-input" 
        role="combobox" 
        tabindex="0">
        ${iconToRender}
        <span class="icon icon-lib-mat-error-round select-icon-error ${reversed ? 'reversed' : ''}"></span>
      </div>
      <div 
        class="combo-menu" 
        role="listbox" 
        id="listbox-${name}" 
        aria-labelledby="combo-label-${name}" 
        tabindex="-1"
      >
      </div>
    `;
  return el;
}

/*
 * Select Component
 * Accepts a combobox element and an array of string options
 */
export default class Select {
  constructor(el, callback, options, parentToAppend) {
    this.el =
      el ||
      createOwnElement({
        name: options.name,
        icon: options.icon,
        reversed: options.reversed,
      });
    this.popupPosition = options.popupPosition || 'bottom-left';
    this.options = options.options || [];
    this.disabledOptions = options.disabledOptions || [];
    this.customSelectedOptions = options.customSelectedOptions || null;
    this.labelEl = this.el.querySelector('.combo-label');
    this.comboEl = this.el.querySelector('[role=combobox]');
    this.listboxEl = this.el.querySelector('[role=listbox]');
    this.parentToAppend = parentToAppend;
    this.defaultMessage = options.defaultMessage || null;
    this.reversed = options.reversed || false;
    this.presentedOptionAsInput = options.presentedOptionAsInput || false;
    this.presentedOptionInputValue = '';
    this.presentedOptionInputPlaceholder = options.presentedOptionInputPlaceholder || '';
    this.presentedOptionInputOnChangeCallback = options.presentedOptionInputOnChangeCallback || null;
    this.presentedOptionInputErrorValidation = options.presentedOptionInputErrorValidation || null;
    this.customComboClickCallback = options.customComboClickCallback || null;
    this.customComboKeyDownCallback = options.customComboKeyDownCallback || null;
    this.customComboBlurCallback = options.customComboBlurCallback || null;
    this.customOptionFocusout = options.customOptionFocusout || null;
    this.validInput = true;

    // data
    this.idBase = this.comboEl.id || 'combo';

    // state
    this.activeIndex = -1;
    this.open = false;
    this.searchString = '';
    this.searchTimeout = null;

    this.required = options.required || false;

    this.callback = callback;
  }

  init() {
    // init
    if (!this.el || !this.comboEl || !this.listboxEl) {
      console.log('Select component is missing required elements');
      return;
    }
    let firstCustomOption = null;
    if (this.customSelectedOptions && !this.defaultMessage) {
      [firstCustomOption] = this.customSelectedOptions;
    }
    if (this.defaultMessage) {
      firstCustomOption = this.defaultMessage;
    }
    const [firstOption] = this.options;
    let firstOptionSpan = document.createElement('p');
    if (this.presentedOptionAsInput) {
      firstOptionSpan = document.createElement('input');
      firstOptionSpan.setAttribute('type', 'text');
      firstOptionSpan.setAttribute('placeholder', this.presentedOptionInputPlaceholder);
      this.comboEl.querySelector('.select-icon-error').classList.add('hidden');

      // add on type event listener
      firstOptionSpan.addEventListener('input', (event) => {
        if (this.presentedOptionInputOnChangeCallback) {
          this.presentedOptionInputValue = event.target.value;
          if (event.target.value === '') {
            this.comboEl.querySelector('.clear-input-icon').classList.add('hidden');
            this.comboEl.querySelector('.select-icon-error').classList.add('hidden');
            this.comboEl.querySelector('.search-input-icon').classList.remove('hidden');
            this.comboEl.classList.remove('error');
          } else if (this.presentedOptionInputErrorValidation) {
            const showError = this.presentedOptionInputErrorValidation(event.target.value);
            if (showError) {
              this.comboEl.querySelector('.select-icon-error').classList.remove('hidden');
              this.comboEl.querySelector('.clear-input-icon').classList.add('hidden');
              this.comboEl.querySelector('.search-input-icon').classList.add('hidden');
              this.comboEl.classList.add('error');
            } else {
              this.comboEl.querySelector('.select-icon-error').classList.add('hidden');
              this.comboEl.querySelector('.clear-input-icon').classList.remove('hidden');
              this.comboEl.querySelector('.search-input-icon').classList.add('hidden');
              this.comboEl.classList.remove('error');
            }
          }

          this.presentedOptionInputOnChangeCallback(event.target.value);
          firstOptionSpan.addEventListener('blur', this.customComboBlurCallback ? this.customComboBlurCallback : this.onComboBlur.bind(this));
          firstOptionSpan.addEventListener('focusout', this.customOptionFocusout ? this.customOptionFocusout : this.onComboBlur.bind(this));

          // add event to clear input icon
          const clearInputIcon = this.comboEl.querySelector('.clear-input-icon');
          clearInputIcon.addEventListener('click', () => {
            firstOptionSpan.value = '';
            this.comboEl.querySelector('.select-icon-error').classList.add('hidden');
            this.comboEl.querySelector('.clear-input-icon').classList.add('hidden');
            this.comboEl.querySelector('.search-input-icon').classList.remove('hidden');
            this.presentedOptionInputOnChangeCallback('');
            firstOptionSpan.focus();
          });
        }
      });
      const clearInputIcon = document.createElement('span');
      clearInputIcon.classList.add('icon', 'icon-lib-close', 'clear-input-icon', 'hidden');
      this.comboEl.append(clearInputIcon);

      const searchInputIcon = document.createElement('span');
      searchInputIcon.classList.add('icon', 'icon-lib-search', 'search-input-icon');
      this.comboEl.append(searchInputIcon);
    }
    firstOptionSpan.classList.add('presented-option');
    if (this.reversed) firstOptionSpan.classList.add('reversed');
    firstOptionSpan.innerText = firstCustomOption || firstOption;
    // select first option by default
    this.comboEl.prepend(firstOptionSpan);

    // add event listeners
    this.comboEl.addEventListener('blur', this.customComboBlurCallback ? this.customComboBlurCallback : this.onComboBlur.bind(this));
    this.listboxEl.addEventListener('focusout', this.customOptionFocusout ? this.customOptionFocusout : this.onComboBlur.bind(this));
    this.comboEl.addEventListener('click', this.customComboClickCallback ? this.customComboClickCallback : this.onComboClick.bind(this));
    this.comboEl.addEventListener('keydown', this.customComboKeyDownCallback ? this.customComboKeyDownCallback : this.onComboKeyDown.bind(this));

    // create options
    this.options.forEach((option, index) => {
      const optionEl = this.createOption(option, index, this.customSelectedOptions?.[index] ?? null);
      if (this.disabledOptions[index]) {
        optionEl.setAttribute('disabled', true);
        optionEl.classList.add('disabled');
      }
      this.listboxEl.appendChild(optionEl);
    });

    if (this.parentToAppend) {
      this.parentToAppend.appendChild(this.el);
    }

    // set popup initial position
    if (this.popupPosition) {
      this.listboxEl.classList.add(this.popupPosition);
    }
    decorateIcons(this.el);
  }

  createOption(optionValue, index, optionLabel = null) {
    const optionEl = document.createElement('div');
    optionEl.setAttribute('role', 'option');
    optionEl.id = `${this.idBase}-${index}`;
    optionEl.className = 'combo-option';
    // optionEl.setAttribute("aria-selected", `${index === 0}`);
    optionEl.setAttribute('tabindex', 0);
    optionEl.innerText = optionLabel || optionValue;

    optionEl.addEventListener('click', (event) => {
      event.stopPropagation();
      this.onOptionClick(index);
    });
    optionEl.addEventListener('mousedown', this.onOptionMouseDown.bind(this));

    return optionEl;
  }

  isOptionDisabled(optionIndex) {
    const options = this.el.querySelectorAll('[role=option]');
    return options[optionIndex].getAttribute('disabled');
  }

  getSearchString(char) {
    // reset typing timeout and start new timeout
    // this allows us to make multiple-letter matches, like a native select
    if (typeof this.searchTimeout === 'number') {
      window.clearTimeout(this.searchTimeout);
    }

    this.searchTimeout = window.setTimeout(() => {
      this.searchString = '';
    }, 500);

    // add most recent letter to saved search string
    this.searchString += char;
    return this.searchString;
  }

  onComboBlur(event) {
    // do nothing if relatedTarget is contained within listboxEl
    if (this.listboxEl.contains(event.relatedTarget)) {
      return;
    }

    // select current option and close
    if (this.open) {
      this.updateMenuState(false, false);
    }
    // this.validateIsRequired()
  }

  onComboClick() {
    this.updateMenuState(!this.open, false);

    // update option disabled state
    const options = this.el.querySelectorAll('[role=option]');
    [...options].forEach((optionEl) => {
      if (optionEl.getAttribute('disabled')) {
        optionEl.classList.add('disabled');
      }
    });
  }

  onComboKeyDown(event) {
    const { key } = event;
    const max = this.options.length - 1;

    const action = getActionFromKey(event, this.open);

    switch (action) {
      case SelectActions.Last:
      case SelectActions.First:
        if (this.validInput) {
          this.updateMenuState(true, false);
        }
      // intentional fallthrough
      case SelectActions.Next:
      case SelectActions.Previous:
      case SelectActions.PageUp:
      case SelectActions.PageDown:
      case SelectActions.Tab:
      case SelectActions.ShiftTab:
        event.preventDefault();
        return this.onOptionChange(getUpdatedIndex(this.activeIndex, max, action));
      case SelectActions.CloseSelect:
        event.preventDefault();
        if (!this.isOptionDisabled(this.activeIndex)) {
          this.selectOption(this.activeIndex);
        }
      // intentional fallthrough
      case SelectActions.Close:
        event.preventDefault();
        return this.updateMenuState(false);
      case SelectActions.Type:
        if (key === 'Backspace' && this.searchString.length === 0) {
          return null;
        }
        return this.onComboType(key);
      case SelectActions.Open:
        event.preventDefault();
        if (this.validInput) {
          return this.updateMenuState(true, false);
        }
        return this.updateMenuState(false);
      default:
        return null;
    }
  }

  onComboType(letter) {
    if (!this.validInput) {
      return;
    }
    // open the listbox if it is closed
    this.updateMenuState(true, false);

    // find the index of the first matching option
    const searchString = this.getSearchString(letter);
    const searchIndex = getIndexByLetter(this.options, searchString, this.activeIndex + 1);

    // if a match was found, go to it
    if (searchIndex >= 0) {
      this.onOptionChange(searchIndex);
    }
    // if no matches, clear the timeout and search string
    else {
      window.clearTimeout(this.searchTimeout);
      this.searchString = '';
    }
  }

  onOptionChange(index) {
    // update state
    this.activeIndex = index;

    // update aria-activedescendant
    this.comboEl.setAttribute('aria-activedescendant', `${this.idBase}-${index}`);

    // update active option styles
    const options = this.el.querySelectorAll('[role=option]');
    [...options].forEach((optionEl) => {
      optionEl.classList.remove('option-current');
    });
    options[index].classList.add('option-current');

    // ensure the new option is in view
    if (isScrollable(this.listboxEl)) {
      maintainScrollVisibility(options[index], this.listboxEl);
    }

    // ensure the new option is visible on screen
    // ensure the new option is in view
    if (!isElementInView(options[index])) {
      options[index].scrollIntoView({ behavior: 'smooth', block: 'nearest' });
    }
  }

  onOptionClick(index) {
    if (this.isOptionDisabled(index)) {
      return;
    }
    this.onOptionChange(index);
    this.selectOption(index);
    this.updateMenuState(false);
  }

  onOptionMouseDown() {
    // Clicking an option will cause a blur event,
    // but we don't want to perform the default keyboard blur action
    this.ignoreBlur = true;
  }

  selectOption(index) {
    // update state
    this.activeIndex = index;

    // update displayed value
    const selected = this.options[index];
    let customSelected = selected;
    if (this.customSelectedOptions) {
      customSelected = this.customSelectedOptions[index];
    }
    if (!this.presentedOptionAsInput) {
      const presentedOption = this.comboEl.querySelector('.presented-option');
      presentedOption.innerHTML = customSelected;
    } else {
      const presentedOption = this.comboEl.querySelector('.presented-option');
      presentedOption.value = customSelected;
    }

    // update aria-selected
    const options = this.el.querySelectorAll('[role=option]');
    [...options].forEach((optionEl) => {
      optionEl.setAttribute('aria-selected', 'false');
    });
    options[index].setAttribute('aria-selected', 'true');
    // this.comboEl.focus();
    if (this.callback) {
      this.callback(index);
    }
  }

  updateMenuState(open, callFocus = true) {
    if (this.open === open) {
      return;
    }

    // update state
    this.open = open;

    // update aria-expanded and styles
    this.comboEl.setAttribute('aria-expanded', `${open}`);
    if (open) {
      this.el.classList.add('open');

      if (this.listboxEl.classList.contains('top-right') || this.listboxEl.classList.contains('top-left')) {
        this.listboxEl.style.setProperty('top', `calc(-100% - ${this.listboxEl.offsetHeight}px)`);
      }
    } else {
      this.el.classList.remove('open');

      // reset active index and styles
      const options = this.el.querySelectorAll('[role=option]');
      [...options].forEach((optionEl) => {
        optionEl.className = 'combo-option';
        // check if optionEl has disabled attribute
        if (optionEl.getAttribute('disabled')) {
          optionEl.classList.add('disabled');
        }
      });
    }

    // update activedescendant
    const activeID = open ? `${this.idBase}-${this.activeIndex}` : '';
    this.comboEl.setAttribute('aria-activedescendant', activeID);

    if (activeID === '' && !isElementInView(this.comboEl)) {
      this.comboEl.scrollIntoView({ behavior: 'smooth', block: 'nearest' });
    }

    // move focus back to the combobox, if needed
    if (callFocus && open) {
      this.comboEl.focus();
    }
  }
}
